<template>
  <div class="dor flex-row-between">
    <div class="text-32">
        可领取DOR数量：{{getBit(utils.formatUnits(availableNum, 18), 5) }}
        <el-icon class="is-loading" v-if="loading">
            <Loading />
        </el-icon>
    </div>
    <div class="flex-row-end">
        <el-button class="dor-btn" :disabled="availableNum <= 0" v-if="isLogin" @click="availableHandler">领取</el-button>
        <el-button class="dor-btn" v-else @click="setConnectDialog(true)">链接钱包</el-button>
        <a href="https://teadao.app/#/swap/Pancake?outputCurrency=0x23766cb8a96ff2f46f664bc7d088a6306de73618" target="_bank" class="flex-column-center align-center buy pointer text-center">
            <img src="@/assets/images/dor.jpg" class="buy-icon"/>
            <div class="MT-5">Buy Token</div>
        </a>
    </div>
  </div>
  
</template>

<script>
import { mapActions } from 'vuex'
import { product } from '@/plugins/wallet/config.json'
import DOR_ABI from '@/api/abis/dor.json'
import { utils } from 'ethers'
import { getBit, Tips } from '../../libs/utils'
import { ElNotification } from 'element-plus'

export default {
    data() {
        return {
            utils,
            getBit,
            isLogin: false,
            availableNum: 0,
            contract: null,
            loading: false
        }
    },
    mounted() {
        this.isLogin = window.wallet.accounts[0] ? true : false
        this.isLogin && this.loginCallBack()
        window.wallet.on('accountChange', () => {
            this.isLogin = window.wallet.accounts[0] ? true : false
            this.isLogin && this.loginCallBack()
        })
    },
    methods: {
        ...mapActions(['setConnectDialog']),
        loginCallBack() {
            this.contract = window.wallet.initContract(product.DOR, DOR_ABI)
            this.loading = true
            this.contract.available('0x23766cb8a96ff2f46f664bc7d088a6306de73618', window.wallet.accounts[0]).then(data => {
                this.availableNum = data
            }).finally(() => {
                this.loading = false
            })
        },
        // 领取奖励
        async availableHandler() {
            try {
                await this.contract.claim('0x23766cb8a96ff2f46f664bc7d088a6306de73618')
                ElNotification({
                    type: 'success',
                    title: 'Success',
                    message: '代币领取成功'
                })   
            } catch (error) {
              Tips(error)  
            }   
        }
    }
}
</script>

<style lang="scss" scoped>
.dor{
    width: 600px;
    max-width: 100%;
    margin: 50px auto;
    .dor-btn{
        height: 50px;
        background: #FCBE2F;
        border: 1px solid #2A2A2A;
        border-radius: 8px;
        width: 100px;
        color: #fff;
        &.is-disabled{
        background: #e4e7ed;
        }
    }
}
.buy{
    width: 80px;
    .buy-icon{
        width: 25px;
        height: 25px;
    }
}
</style>